.container {
    position: relative;
    background-color: rgba(240, 239, 244, 1);
    width: 672px;
    height: 736px;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    /*   align-items: center;
    flex-direction: column; */
    box-sizing: border-box;
    /*   padding-left: 118px;
    padding-right: 118px; */
  }
  .box {
    position: absolute;
    top: 32px;
    left: 32px;
  }
  .wrapper {
    display: flex;
    justify-content: center;
    margin-top: 32px;
    margin-bottom: 32px;
  }
  .wrapper {
    margin-left: 32px;
    margin-right: 32px;
  }
  .hideOnTablets {
    display: none;
  }
  
  /* Mobile and > */
  @media screen and (min-width: 320px) {
    .container {
      height: 780px;
      padding-left: 16px;
      padding-right: 16px;
      /*   padding-left: 16px;
      padding-right: 16px; */
    }
    .box {
      position: absolute;
      top: 24px;
      left: 16px;
    }
    .wrapper {
      margin: 16px;
    }
  }
  
  @media screen and (min-width: 375px) {
    .container {
      height: 780px;
      /*   margin-left: 16px;
      margin-right: 16px; */
    }
    .box {
      position: absolute;
      top: 24px;
      left: 16px;
    }
  }
  
  /* Tablet and > */
  @media screen and (min-width: 768px) {
    .container {
      height: 960px;
      min-width: 704px;
    }
    .box {
      position: absolute;
      top: 32px;
      left: 32px;
    }
    .wrapper {
      margin: 32px;
    }
  }
  
  /* Desktop and > */
  @media screen and (min-width: 1440px) {
    .hideOnTablets {
      display: contents;
    }
  
    .wrapper {
      gap: 32px;
    }
    .container {
      height: 736px;
      min-width: 672px;
    }
    .box {
      position: absolute;
      top: 32px;
      left: 32px;
    }
  }
  