@media screen and (min-width: 320px) {
  .logo {
    color: rgb(50, 63, 71);
    font-family: Poppins, sans-serif;
    text-align: center;
    font-size: 18px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: -0.0018em;
    text-transform: uppercase;
  }
}

@media screen and (min-width: 768px) {
  .logo {
    font-size: 24px;
    line-height: 1;
    letter-spacing: -0.0024em;
    left: 32px;
    top: 38px;
  }
}

@media screen and (min-width: 1440px) {
  .logo {
    left: 33px;
    top: 32px;
  }
}
